<template>

  <div v-if="store.isLogin" class="post-msg">
    <a-textarea
        class="postMsg"
        v-model:value="feedForm.content"
        placeholder="有什么想要分享给大家？"
        :auto-size="{ minRows: 2, maxRows: 5 }"
        @input="disabled = feedForm.content === ''"
        id="feedInput"
    />


    <el-upload
        ref="uploadRef"
        action="/upload"
        :data="{'operationCode': 2}"
        :headers="{ 'Authorization': token }"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
        :on-error="uploadError"
        :auto-upload="false"
        :on-change="imgChange"
    >
    </el-upload>
    <div class="pic-box">
      <img :src="tempPic" alt=""/>
      <div class="overlay">
        <el-icon @click="removePic" style="cursor: pointer">
          <Delete/>
        </el-icon>
      </div>
    </div>
    <div>
      <a-button class="picture-btn" @click="selectPic">图片
        <template #icon>
          <PictureOutlined/>
        </template>
      </a-button>

      <a-popover trigger="click" placement="bottom">
          <a-button class="face-btn">表情
            <template #icon>
              <SmileOutlined/>
            </template>
          </a-button>
        <template #content>
          <EmojiPicker
              :native="true"
              v-show="true"
              :i18n="i18n"
              @select="onSelectEmoji"
          />
        </template>
      </a-popover>

      <a-button class="post-btn" shape="round" :disabled="disabled" @click="postFeed">发布</a-button>
    </div>
  </div>



  <div v-infinite-scroll="loadFeeds" :infinite-scroll-disabled="disableLoadFeeds" :infinite-scroll-immediate="false">
    <a-card v-for="(item,index) in feed" :key="index" class="post" size="default">
      <a-card-meta>
        <template #title>
          <div class="post-nickname" @click="toUserCenter(item.userId)" style="cursor: pointer">{{
              item.userNickName
            }}
          </div>
        </template>

        <template #description>
          <div class="post-createtime">{{ item.createTime }}</div>
        </template>

        <template #avatar>
          <a-avatar :size="60" :src="item.userAvatar" @click="toUserCenter(item.userId)"
                    style="cursor: pointer"></a-avatar>
        </template>
      </a-card-meta>


      <div class="post-content">
        <a-typography-paragraph :ellipsis="ellipsis ? { rows: 5, expandable: true } : false"
                                :style="{lineHeight: '25px',fontSize:'16px'}" :content="item.content">
          <template #ellipsisSymbol>
            <span :style="{color:'#de1c31'}">展开</span>
          </template>
        </a-typography-paragraph>
      </div>


      <a-image
          :src="item.imageUrl"
          :lazy="true"
      />

      <template #actions>
        <a-button>
          <template #icon>
            <ShareAltOutlined key="forward"/>
          </template>
        </a-button>

        <a-button @click="toFeedDetail(item.feedId)">
          <template #icon>
            <CommentOutlined key="comment"/>
          </template>
          {{ item.comments.length }}
        </a-button>


        <a-button @click="unLikeFeed(item)" v-if="item.likedUserIds.includes(store.userInfo.userId)">
          <template #icon>
            <LikeOutlined style="color: #de1c31" key="like"/>
          </template>
          {{ item.likedUserIds.length }}
        </a-button>
        <a-button @click="likeFeed(item)" v-else>
          <template #icon>
            <LikeOutlined key="like"/>
          </template>
          {{ item.likedUserIds.length }}
        </a-button>


      </template>
    </a-card>
  </div>
  <!--加载显示，正文不显示-->
  <a-card :loading="loading" v-if="loading" class="post" size="default">
    <a-card-meta>
      <template #title>
        <div class="post-nickname">加载动态</div>
      </template>

      <template #description>
        <div class="post-createtime">加载动态</div>
      </template>

      <template #avatar>
        <a-avatar :size="60"></a-avatar>
      </template>
    </a-card-meta>

    <div class="post-content">
      <a-typography-paragraph :ellipsis="ellipsis ? { rows: 5, expandable: true } : false"
                              :style="{lineHeight: '25px',fontSize:'16px'}" content="加载动态">
        <template #ellipsisSymbol>
          <span :style="{color:'#de1c31'}">展开</span>
        </template>
      </a-typography-paragraph>
    </div>
    <a-image/>
  </a-card>
  <a-card :loading="loading" v-if="loading" class="post" size="default">
    <a-card-meta>
      <template #title>
        <div class="post-nickname">加载动态</div>
      </template>

      <template #description>
        <div class="post-createtime">加载动态</div>
      </template>

      <template #avatar>
        <a-avatar :size="60"></a-avatar>
      </template>
    </a-card-meta>

    <div class="post-content">
      <a-typography-paragraph :ellipsis="ellipsis ? { rows: 5, expandable: true } : false"
                              :style="{lineHeight: '25px',fontSize:'16px'}" content="加载动态">
        <template #ellipsisSymbol>
          <span :style="{color:'#de1c31'}">展开</span>
        </template>
      </a-typography-paragraph>
    </div>
    <a-image/>
  </a-card>
  <a-card :loading="loading" v-if="loading" class="post" size="default">
    <a-card-meta>
      <template #title>
        <div class="post-nickname">加载动态</div>
      </template>

      <template #description>
        <div class="post-createtime">加载动态</div>
      </template>

      <template #avatar>
        <a-avatar :size="60"></a-avatar>
      </template>
    </a-card-meta>

    <div class="post-content">
      <a-typography-paragraph :ellipsis="ellipsis ? { rows: 5, expandable: true } : false"
                              :style="{lineHeight: '25px',fontSize:'16px'}" content="加载动态">
        <template #ellipsisSymbol>
          <span :style="{color:'#de1c31'}">展开</span>
        </template>
      </a-typography-paragraph>
    </div>
    <a-image/>
  </a-card>

  <div :class="noMore ? 'no-more-ture':'no-more-false'">没有更多了</div>

</template>

<script setup>
import {CommentOutlined, LikeOutlined, PictureOutlined, ShareAltOutlined, SmileOutlined,} from '@ant-design/icons-vue';
import {onMounted, reactive, ref} from "vue"
import requestService from "@/utils/request";
import {store} from "@/store";
import {ElMessage} from "element-plus";
import {getLocalStorageItem} from "@/utils/data";

import {Delete} from '@element-plus/icons-vue'
import {notification} from "ant-design-vue";
import router from "@/router";

import EmojiPicker from 'vue3-emoji-picker'
import 'vue3-emoji-picker/css'

const i18n= ref({
  search: '请输入关键字',
  categories: {
    Activity: "Atividades",
    Flags: "旗帜",
    Foods: "食物",
    Frequently: "Frequentes",
    Objects: "Objetos",
    Nature: "Natureza",
    Peoples: "Pessoas",
    Symbols: "Símbolos",
    Places: "Locais"
  }
})
function onSelectEmoji(emoji) {

  let input = document.getElementById("feedInput")
  let startPos = input.selectionStart
  let endPos = input.selectionEnd
  let resultText = input.value.substring(0, startPos) + emoji.i + input.value.substring(endPos)
  input.value = resultText
  input.focus()
  input.selectionStart = startPos + emoji.n.length
  input.selectionEnd = startPos + emoji.n.length
  feedForm.content = resultText
  disabled.value = feedForm.content === ''

}

const toFeedDetail = (feedId) => {
  router.push({path: '/postDetail', query: {feedId: feedId}})
}

const loading = ref(true);
const {searchFeed} = defineProps({
  feed: {
    type: Array,
    default: []
  }
})
onMounted(() => {
  loadFeeds();
})

const ellipsis = ref(true);

const iconColor = ref("#000")

const toUserCenter = (userId) => {
  router.push({path: '/userCenter/otherUserCenter', query: {userId: userId}})
}

const changeColor = () => {
  iconColor.value = iconColor.value === '#000' ? "#de1c31" : "#000"
}

const feedForm = reactive({
  content: "",
  imageUrl: ""
})

const feed = ref([
  {
    comments: [],
    likedUserIds: [],
  }
])

const startPage = ref(1);
const pageSize = ref(10);
const disableLoadFeeds = ref(false);
const noMore = ref(false);
const loadFeeds = () => {
  disableLoadFeeds.value = true;
  loading.value = true;
  requestService.get("/feed/getFeedByPage/" + startPage.value + "/" + pageSize.value).then((res) => {
    if (res.data.code === 2001) {
      if (startPage.value === 1) {
        setTimeout(() => {
          feed.value = res.data.data;
          startPage.value++;
          disableLoadFeeds.value = false;
          loading.value = false;
        }, 1500)
      } else {
        setTimeout(() => {
          feed.value = feed.value.concat(res.data.data);
          startPage.value++;
          disableLoadFeeds.value = false;
          loading.value = false;
        }, 1500)
      }
    } else {
      loading.value = false;
      setTimeout(() => {
        noMore.value = false;
      }, 3000)
      noMore.value = true;
      setTimeout(() => {
        disableLoadFeeds.value = false;
      }, 5000)
    }
  })
}


const disabled = ref(true);
const tempPic = ref('')

const dialogImageUrl = ref('')
const dialogVisible = ref(false)


const handleAvatarSuccess = async function (res, upload) {
  if (res.code === 1001) {
    ElMessage({
      type: 'success',
      message: '上传成功'
    })
    feedForm.imageUrl = res.data.filePath
    submitFeed()
  } else {
    ElMessage({
      type: 'warning',
      message: res.msg
    })
  }
};

const uploadError = () => {
  ElMessage({
    type: 'error',
    message: '上传失败'
  })
}
const token = ref("")

const beforeAvatarUpload = function (rawFile) {
  token.value = getLocalStorageItem("token")
  if (rawFile.type !== 'image/jpeg' && rawFile.type !== 'image/png' && rawFile.type !== 'image/gif') {
    ElMessage.error('头像只允许JPG、PNG或GIF格式!');
    return false;
  } else if (rawFile.size / 1024 / 1024 > 10) {
    ElMessage.error('上传的头像文件大小不能 10MB!');
    return false;
  }
  return true;
};

const uploadRef = ref();

function uploadPic() {
  return new Promise((resolve, reject) => {
    uploadRef.value.submit();
    resolve()
  })
}

function selectPic() {
  uploadRef.value.$el.querySelector('input').click()
}

function removePic() {
  tempPic.value = ''
  uploadRef.value.clearFiles()
}


const imgChange = (file, fileList) => {
  tempPic.value = URL.createObjectURL(file.raw)
}

const postFeed = () => {
  if (tempPic.value) {
    uploadPic()
  } else {
    submitFeed()
  }
}

const submitFeed = () => {
  requestService.post("/feed", feedForm).then((res) => {
    if (res.data.code === 3001) {
      openNotificationWithIcon('success', res.data.msg)
      feedForm.content = ""
      feedForm.imageUrl = ""
      removePic()
      let newFeed = [res.data.data];
      feed.value = newFeed.concat(feed.value)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}

const likeFeed = (item) => {
  requestService.post("/like",
      {
        feedId: item.feedId,
        feedUserId: item.userId
      },
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res) => {
    if (res.data.code === 3001) {
      openNotificationWithIcon('success', res.data.msg)
      item.likedUserIds.push(store.userInfo.userId)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}
const unLikeFeed = (item) => {
  requestService.delete("/like",
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'},
        data: {
          feedId: item.feedId,
          feedUserId: item.userId
        }
      }
  ).then((res) => {
    if (res.data.code === 5001) {
      openNotificationWithIcon('success', res.data.msg)
      // 将用户id从feed的likedUserIds中删除
      item.likedUserIds.splice(item.likedUserIds.indexOf(store.userInfo.userId), 1)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}


const openNotificationWithIcon = (type, title, description) => {
  notification[type]({
    message: title,
    description: description,
    duration: 2
  });
};

</script>

<style scoped>


.post {
  margin-bottom: 20px;
  min-width: 790px;
  max-width: 790vw;
  width: 790px;
}

.post button {
  border: none;
  box-shadow: none;
}

.post button:hover {
  color: #de1c31;
}

/*消除按钮点击样式*/
:global(.ant-wave) {
  display: none !important;
}

.post-nickname {
  font-size: 19px;
  min-width: 100px;
}

.post-createtime {
  font-size: 15px;
}

.post-content {
  margin: 20px 0;
  white-space: pre-wrap;
}


.post-msg {
  border-radius: 8px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 15px 10px;
  background-color: white;
}

.picture-btn, .face-btn {
  margin-top: 10px;
  border: none;
  box-shadow: none;
}

.picture-btn:hover, .face-btn:hover, .post-btn:hover {
  color: #de1c31;
}

.post-btn:hover {
  border: 1px #de1c31 solid;
}

.post-btn:disabled:hover {
  color: rgb(184, 184, 184) !important;
  border: 1px rgb(217, 217, 217) solid !important;
}

.post-btn {
  margin-top: 10px;
  float: right;
}

textarea.ant-input:hover, textarea:focus {
  border: 1px #de1c31 solid;
  -webkit-box-shadow: none;
  box-shadow: none;
}

textarea {
  border: 1px rgb(245, 245, 245) solid;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.pic-box {
  position: relative;
  max-width: 300px;
}

.pic-box img {
  width: 100%;
  height: auto;
  display: block;
}

.overlay {
  position: absolute;
  background-color: rgba(255, 255, 255, 0);
  color: rgba(255, 255, 255, 0);
  font-size: 65px;
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .5s ease;
}

.overlay:hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 35px;
  font-weight: bold;
}

.no-more-ture {
  width: 100%;
  height: 150px;
  text-align: center;
  line-height: 150px;
  font-size: 25px;
  transition: .5s ease;
}

.no-more-false {
  width: 100%;
  height: 0;
  text-align: center;
  line-height: 150px;
  font-size: 0;
  transition: .5s ease;
}
</style>